import s from "../ProfileInfo.module.css";
import {FC} from "react";

type profileDataType = {
    name: string,
    facebook: string,
    website: string,
    vk: string,
    twitter: string,
    instagram: string,
    youtube: string,
    github: string,
    mainLink: string,
    lookingForAJob: boolean,
    lookingForAJobDescription: string
}

const ProfileData: FC<profileDataType> = ({name, facebook, website, vk, twitter, instagram, youtube,
                         github, mainLink, lookingForAJob, lookingForAJobDescription
}) =>{
    return (
        <div className={s.description}>
            <div className={s.name}>
                {name}
            </div>
            <div className={s.data}>
                <ul>
                    <li></li>
                    <li>Facebook: {facebook}</li>
                    <li>website: {website}</li>
                    <li>vk: {vk}</li>
                    <li>twitter: {twitter}</li>
                    <li>instagram: {instagram}</li>
                    <li>youtube: {youtube}</li>
                    <li>github: {github}</li>
                    <li>mainLink: {mainLink}</li>
                    <li>: {lookingForAJob ? "yes" : "no"}</li>
                    <li>: {lookingForAJobDescription}</li>
                </ul>
            </div>
        </div>
    );
}

export default ProfileData;