import s from './login.module.css';
import {connect} from "react-redux";
import {LoginThunk} from "../../redux/auth-reducer";
import {Navigate} from "react-router-dom";
import {useForm} from "react-hook-form";
import {appStateType} from "../../redux/redux-store";
import {FC} from "react";

type mapDispatchToPropsType = {
    LoginThunk: (email: string, password: string, rememberMe: boolean, captcha: string | null) => void
}
type mapStateToPropsType = {
    isAuth: boolean,
    captchaUrl: string | null
}

type loginFormType = mapDispatchToPropsType & mapStateToPropsType

const LoginForm: FC<loginFormType> = ({LoginThunk, isAuth, captchaUrl}) => {

    const {
        handleSubmit,
        register,
        formState: {
            errors
        }
    } = useForm({
        mode: "onSubmit"
    })

    const onSubmit = (formData: any) => {
        LoginThunk(formData.email, formData.password, formData.rememberMe, formData.captcha)
    }

    if (isAuth) {
        return <Navigate to={'/profile'}/>
    }

    return (
        <form className={s.form} onSubmit={handleSubmit(onSubmit)}>
            
            <div>
                <input className={s.login} {...register('email', {required: true})} type="email" placeholder="email"/>
                {errors?.email && <> {errors.email.message || "error!"}</>}
            </div>
            <div>
                <input className={s.password} {...register('password', {required: true})} type="password" placeholder="password"/>
                {errors?.email && <> {errors.email.message || "error!"}</>}
            </div>
            <div className={s.check}>
                <input {...register('rememberMe', {required: false})} className={s.checkbox} type="checkbox"/>
                <div> </div>
            </div>
            {captchaUrl &&
                <div>
                    <img src={captchaUrl}/>
                    <input className={s.captcha} {...register('captcha', {required: true})} type='text'/>
                </div>}
            <div>
                <button className={s.submit}></button>
            </div>
        </form>
    )
}
const mapStateToProps = (state: appStateType):mapStateToPropsType => ({
    isAuth: state.auth.isAuth,
    captchaUrl: state.auth.captchaUrl
})


export default connect(mapStateToProps, {LoginThunk})(LoginForm);