import {addMessage, dialogsMessageType, dialogsNameType} from "../../redux/dialogs-reducer";
import Dialogs from "./Dialogs";
import {connect} from "react-redux";
import {withAuthRedirect} from "../../hoc/withAuthRedirect";
import {compose} from "redux";
import {appStateType} from "../../redux/redux-store";
import { Component } from "react";

type mapStateToPropsType = {
    dialogsName: Array<dialogsNameType>
    dialogsMessage: Array<dialogsMessageType>
    newPostText: string
}
let mapStateToProps = (state: appStateType):mapStateToPropsType => {
    return {
        dialogsName: state.dialogsPage.dialogsName,
        dialogsMessage: state.dialogsPage.dialogsMessage,
        newPostText: state.dialogsPage.newMessage,
    }
}

export default compose<Component>(connect(mapStateToProps, {addMessage}), withAuthRedirect)(Dialogs);