import React, {FC, useState} from 'react';
import s from "./users.module.css";
import cn from 'classnames'

type PropsType = {
    totalUsersCount: number
    pageSize: number
    currentPage: number
    onPageChanged: (pageNumber: number) => void
    portionSize?: number
}

const Paginate: FC<PropsType> = ({totalUsersCount, pageSize, currentPage, onPageChanged, portionSize = 10}) => {

    let pagesCount = Math.ceil(totalUsersCount / pageSize);

    let pages = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i);
    }

    let portionCount = Math.ceil(pagesCount / portionSize);
    let [portionNumber, setPortionNumber] = useState(1);

    let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1;
    let rightPortionPageNumber = portionNumber * portionSize;

    return (
        <div className={s.pagination}>

            { portionNumber > 1 &&
                <button className={s.prev} onClick={() => { setPortionNumber(portionNumber - 1) }}>PREV</button> }

            {pages
                .filter(p => p >= leftPortionPageNumber && p<=rightPortionPageNumber)
                .map(p => {
                return (<span key={p} className={cn((currentPage) === p && s.checked)} onClick={(e) => {onPageChanged(p)}}>{p}</span>);
            })}

            { portionCount > portionNumber &&
                <button className={s.next} onClick={() => { setPortionNumber(portionNumber + 1) }}>NEXT</button> }

        </div>
    );
}

export default Paginate;