import {instance, resultCodes} from "./instance";
import {profileType} from "../types/types";

type profileInfoType = profileType
type getStatusType = {
    userId: number
}
type updateStatusType = {
    resultCode: resultCodes
    messages: Array<string>
    data: {}
}
type savePhotoType = {
    data: {
        small: string,
        large: string
    }
    resultCode: resultCodes
    messages: Array<string>
}
type saveProfileType = {
    resultCode: resultCodes
    messages: Array<string>
    userId: number
    data:{
        lookingForAJob: boolean
        lookingForAJobDescription: string
        fullName: string
        contacts: {
            github: string,
            vk: string,
            facebook: string,
            instagram: string,
            twitter: string,
            website: string,
            youtube: string,
            mainLink: string
        }
    }
}

export const profileApi ={
    profileInfo(id: number){
        return instance.get<profileInfoType>(`profile/` + id)
            .then(response => {
                return response.data
            })
    },
    getStatus(id: number){
        return instance.get(`profile/status/` + id)
    },
    updateStatus(status: string){
        return instance.put<updateStatusType>(`profile/status/`, {status: status})
    },
    savePhoto(photoFile: any){
        let formData = new FormData();
        formData.append("image", photoFile)
        return instance.put(`profile/photo`, formData, {
            headers:{
                'Content-Type': 'multipart/form-data'
            }
        })
    },
    saveProfile(profile: profileType){
        return instance.put<saveProfileType>(`profile`, profile)
    }
}
