import s from "./Dialogs.module.css";
import {NavLink} from "react-router-dom";
import React, {FC} from "react";

type DialogType = {
    name: string
    id: number
}

const DialogItem: FC<DialogType> = ({name, id}) => {
    let path = '/dialogs/' + id;
    return (
        <li className={s.item}><NavLink to={path}>{name}</NavLink></li>
    );
};

export default  DialogItem;