import Header from "./Header";
import {connect} from "react-redux";
import {LogoutThunk, setAuthUserData} from "../../redux/auth-reducer";
import Preloader from "../common/preloader";
import {compose} from "redux";
import {appStateType} from "../../redux/redux-store";
import {FC} from "react";

type mapStateToPropsType = {
    isAuth: boolean
    login: string | null
    isFetching: boolean
}
type mapDispatchToPropsType = {
    setAuthUserData: (userId: number | null, email: string | null, login: string | null, isAuth: boolean) => void
    LogoutThunk: () => void
}
type headerContainerType = mapStateToPropsType & mapDispatchToPropsType

const HeaderContainer: FC<headerContainerType> = ({isFetching, ...props}) => {
    return <>
        {isFetching ? <Preloader/> : <Header {...props}/>}
    </>
}

const mapStateToProps = (state: appStateType): mapStateToPropsType => ({
    isAuth: state.auth.isAuth, login: state.auth.login, isFetching: state.auth.isFetching,
})
export default compose(connect(mapStateToProps, {setAuthUserData, LogoutThunk}))(HeaderContainer);
