import {instance} from "./instance";



export const UsersApi = {
    getUsersApi(currentPage: number, pageSize: number, term: string = '', friend: null | boolean = null) {
        return instance.get(`users?page=${currentPage}&count=${pageSize}&term=${term}` + (friend === null ? '': `&friend=${friend}`))
            .then(response => {
                return response.data
            })
    },

    onPageChangedApi(currentPage: number, pageSize: number, term: string = '', friend: null | boolean = null) {
        return instance.get(`users?page=${currentPage}&count=${pageSize}&term=${term}` + (friend === null ? '': `&friend=${friend}`))
            .then(response => {
                return response.data
            })
    },
    unfollowApi(id: number) {
        return instance.delete(`follow/${id}`)
            .then(response => {
                return response.data
            })
    },
    followApi(id: number) {
        return instance.post(`follow/${id}`)
            .then(response => {
                return response.data
            })
    }
}



