import React, {FC, useState} from 'react';
import s from './ProfileInfo.module.css';
import StatusWithHooks from "./Status/statusWithHooks";
import ProfileData from "./ProfileData/ProfileData";
import ProfileDataForm from "./ProfileData/ProfileDataForm";
import {profileType} from "../../../types/types";

type profileInfoType = {
    savePhoto: (file: any) => void,
    saveProfile: (profile: profileType) => Promise<any>,
    srcImg: string | null,
    alt: string,
    isOwner: boolean,
    status: string,
    updateStatus: (status: string) => void,
    name: string,
    facebook: string,
    website: string,
    vk: string,
    twitter: string,
    instagram: string,
    youtube: string,
    github: string,
    mainLink: string,
    lookingForAJob: boolean,
    lookingForAJobDescription: string
}

const ProfileInfo: FC<profileInfoType> = ({
                                              savePhoto, srcImg, alt, isOwner, status, updateStatus,
                                              saveProfile, name, facebook, website, vk, twitter,
                                              instagram, youtube, github, mainLink, lookingForAJob,
                                              lookingForAJobDescription
}) => {

    const onMainProfilePhotoSelected = (e: any) => {
        if (e.target.files.length) {
            savePhoto(e.target.files[0]);
        }
    }

    let [editMode, setEditMode] = useState(false);

    const activateEditMode = () => {
        setEditMode(true);
    }

    const deActivateEditMode = () => {
        setEditMode(false);
    }

    const onSubmit = (formData: profileType) => {
        saveProfile(formData).then(() => {
            deActivateEditMode();
        })
    }

    return (
        <div className={s.profile}>
            <div className={s.avatar}>
                <img src={srcImg as string} alt={alt}/>
                {isOwner
                    ? <div className={s.downloadFile}>
                        <input className={s.downloadInput} type="file" name="file" id="inputfile"
                               onChange={onMainProfilePhotoSelected}/>
                        <label className={s.downloadLabel} htmlFor="inputfile"> </label>
                    </div>
                    : ""}
            </div>
            {!editMode
                ? <div>
                    <ProfileData
                        name={name}
                        facebook={facebook}
                        website={website}
                        vk={vk}
                        twitter={twitter}
                        instagram={instagram}
                        youtube={youtube}
                        github={github}
                        mainLink={mainLink}
                        lookingForAJob={lookingForAJob}
                        lookingForAJobDescription={lookingForAJobDescription}
                    />
                    {isOwner ?
                        <button className={s.buttonProfile} onClick={activateEditMode}> </button> : ""}
                </div>
                : <div>
                    <ProfileDataForm onSubmit={onSubmit}/>
                    <button className={s.buttonProfile} onClick={deActivateEditMode}>   
                    </button>
                </div>
            }
            <StatusWithHooks status={status} updateStatus={updateStatus} isOwner={isOwner}/>
        </div>
    );
}
export default ProfileInfo;