import React, {FC} from 'react';
import s from './Dialogs.module.css';
import {useForm} from "react-hook-form";
import Message from "./MessageItem";
import DialogItem from "./DialogsItem";
import {dialogsMessageType, dialogsNameType} from "../../redux/dialogs-reducer";

type dialogsType = {
    dialogsName: Array<dialogsNameType>
    dialogsMessage: Array<dialogsMessageType>
    addMessage: (newMessage: string) => void
}
const Dialogs: FC<dialogsType> = ({dialogsName, dialogsMessage, addMessage}) => {

    const {handleSubmit, register, formState: {errors}, reset} = useForm({mode: "onSubmit"})

    let dialogElements = dialogsName.map(d => <DialogItem name={d.name} key={d.id} id={d.id}/>);

    let messageElements = dialogsMessage.map(m => <Message key={m.id} message={m.message}/>)

    const onSubmit = (formData: any) => {
        addMessage(formData.newMessage)
        reset()
    }

    return (
        <div className={s.dialogs}>
            <div className={s.dialog}>
                <ul>
                    {dialogElements}
                </ul>
            </div>
            <div className={s.message}>
                <ul>
                    {messageElements}
                </ul>
                <form onSubmit={handleSubmit(onSubmit)}>
                    <div className={s.messageInput}>
                        <input className={s.messageInputTextarea} {...register('newMessage', {required: ' - '})}/>
                        {errors?.newMessage && <> {errors.newMessage.message || "error!"}</>}
                        <button className={s.messageInputButton}></button>
                    </div>
                </form>
            </div>
        </div>
    );
};

export default Dialogs;