import React, {FC, useEffect, useState} from 'react';

type statusWithHooksType = {
    status: string
    updateStatus: (status: string) => void
    isOwner: boolean
}

const StatusWithHooks: FC<statusWithHooksType> = (props) => {

    let [editMode, setEditMode] = useState(false);
    let [status, setStatus] = useState(props.status);

    useEffect(() =>{
        setStatus((props.status))
    }, [props.status])

    const activateEditMode = () =>{
        setEditMode(true);
    }
    const deactivateEditMode = () =>{
        setEditMode(false);
        props.updateStatus(status);
    }
    const onStatusChange = (e: any) =>{
        setStatus(e.currentTarget.value)
    }

    return (
        <div>
            {!editMode
                ? <div>
                    <div>status: {!props.status ? " " : props.status}</div>
                    {props.isOwner ? <button onClick={activateEditMode}> </button> : ""}
                </div>

                : <div>
                    <input onChange={onStatusChange} autoFocus={true}
                           value={status}/>
                    <button onClick={deactivateEditMode}> </button>
                </div>
            }
        </div>
    );
}

export default StatusWithHooks;