import React, {FC, useEffect} from 'react';
import s from './users.module.css';
import photoUsersDefault from '../../img/Users/default-users-photo.png';
import {NavLink} from "react-router-dom";
import Preloader from "../common/preloader";
import Paginate from "./Paginate";
import Search from "./Search";
import {useDispatch, useSelector} from "react-redux";
import {getCurrentPage, getFollowingInProgress, getIsFetching, geTotalUsersCount, getPageSize, getUsers, getUsersFilter} from "../../redux/users-selectors";
import {filterType, getUsersThunk, onPageChangedThunk, follow, unfollow} from '../../redux/users-reducer';

export const Users = () => {

    const totalUsersCount = useSelector(geTotalUsersCount)
    const currentPage = useSelector(getCurrentPage)
    const pageSize = useSelector(getPageSize)
    const users = useSelector(getUsers)
    const isFetching = useSelector(getIsFetching)
    const followingInProgress = useSelector(getFollowingInProgress)
    const filter = useSelector(getUsersFilter)

    const dispatch: any = useDispatch()

    useEffect(() => {
        dispatch(getUsersThunk(currentPage, pageSize, filter))
    }, [])

    const onPageChanged = (pageNumber: number) => {dispatch(onPageChangedThunk(pageNumber, pageSize, filter))}
    const onFilterChanged = (filter: filterType) =>{dispatch(getUsersThunk(1, pageSize, filter))}
    const followS = (id: number) => {dispatch(follow(id))}
    const unfollowS = (id: number) => {dispatch(unfollow(id))}

    return (<div className={s.users}>
        <h3 className={s.h_title}> </h3>
        <Search onFilterChanged={onFilterChanged}/>
        {/**/}
        <Paginate currentPage={currentPage} onPageChanged={onPageChanged} totalUsersCount={totalUsersCount} pageSize={pageSize}/>
        {/**/}
        {isFetching ? <Preloader/> :
        users.map((u) => {
            return (<div className={s.user} key={u.id}>
                <div className={s.title}>
                    <div className={s.photo}>
                        <NavLink to={'/profile/' + u.id}>
                            <img src={u.photos.small != null ? u.photos.small : photoUsersDefault} alt=''/>
                        </NavLink>
                    </div>
                    <div className={s.following}>
                        {u.followed
                            ? <button disabled={followingInProgress.some(id => id === u.id)} onClick={() => {unfollowS(u.id)}} className={s.unfollow}>  </button>
                            : <button disabled={followingInProgress.some(id => id === u.id)} onClick={() => {followS(u.id)}} className={s.follow}>  </button>}
                    </div>
                </div>
                <div className={s.description}>
                    <div className={s.name}>
                        {u.name}
                    </div>
                    <div className={s.status}>{u.status}</div>
                </div>
                <div className={s.location}>
                        <span className={s.country}>
                            {/*{u.location.country}*/}
                        </span>
                    {/**/}
                    ,&nbsp;
                    {/**/}
                    <span className={s.city}>
                            {/*{u.location.city}*/}
                        </span>
                </div>
            </div>)
        })}
    </div>);
}
